        padding: 10px;
        width: 100%;
        background: #333;
        -webkit-animation: drop .5s;
                animation: drop .5s;
        z-index: 10;
}
.header .navbar-mobile.active .dropdown li {
          display: block;
          border-right: none;
          margin-bottom: 10px;
}
.header .navbar-mobile.active .dropdown li a {
            font: 400 20px Helvetica, Arial, sans-serif;
            color: #fff;
}
@-webkit-keyframes drop {
0% {
    margin-top: -200px;
}
100% {
    margin-top: 10px;
}
}
@keyframes drop {
0% {
    margin-top: -200px;
}
100% {
